    width: 110px;
    /*display: flex;*/
    /*flex-direction: column;*/
    /*justify-content: flex-end;*/
  }
  .jrtj .jrtj_box img{
    width: 95%;
    height: 6rem;
    margin: 0 auto;
  }
  .jrtj_box>div>span{
    display: block;
  }
  .jrtj_box>div>div>span{
    float: left;
    font-size:15px;
    color: #e12532;
  }
  .jrtj_box>div>div>del{
    position: absolute;
    right: 5px;
    bottom:2px;
    font-size: 12px;
    color: #a8a9a9;
  }
  .hjzb{
    width: 40%;
    margin: 2rem auto 0;
    margin-bottom: 10px;
    position: relative;
    color: #e12532;
  }
  .hjzb>img{
    display: block;
    position: absolute;
    z-index: 0;
    width: 100%;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  .good-list{
    width: 95%;
    text-align: left;
    margin: 0 auto;
    margin-bottom: 10px;
  }
  .good-list:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .list:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .good-list>img{
    float: left;
    width: 50%;
  }
  .good-list>div{
    width: 48%;
    float: left;
    text-align:left;
    margin-left:2%;
  }
  .good-list>div>span{
    font-family: 黑体;
    font-size: 18px;
    font-weight: bold;
  }
  .good-list>div>div>span:first-child {
    font-size: 20px;
    color:#e12532;
  }
  .good-list>div>div>span:last-child{
    font-size: 13px;
    float:right;
    line-height: 26px;
    color: #a8a9a9;
  }
  .good-list>div>div{
  margin-top: 15px;
  }
  .good-list>div>div:after{
    content:"";
    display:block;
    height:0;
    clear:both;
    visibility:hidden;
  }
  .lq{
    width: 100%;
  }
  .lq:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .lq>div{
    width: 40%;
    position: relative;
  }
  .lq>div:first-child{
    float: left;
    height:30px;
  }
  .lq>div>img{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    display: block;
    width: 100%;
  }
  .lq>div>span{
    z-index: 1;
    text-align: right;
    width: 50px;
    line-height:25px;
    position: absolute;
    right:5px;
    color: #ffffff;
    font-size: 14px;
  }
  .yg{
    width: 100%;
    font-size: 12px;
  }
  .yg>div:first-child{
    float: left;
    padding: 2px 5px;
    background: #ffdcdc;
    color: #e12532;
    border-radius:7px;
  }
  .yg>div:last-child{
    float: right;
    padding: 2px 5px;
    background: linear-gradient(to right,#ff722d, #ff4b26 , #ff2820); /* 标准的语法 */
    border-radius:7px;
    color: #ffffff;
  }
  body{
    background: #ffff;
  }
  #index .van-search{
    width: 80%;
    margin: 0 auto;
    background: #ffffff!important;
  }
  #index .van-search .van-cell{
    border-radius: 25px;
    background: #f7f7f7;
    padding:3px 10px;
  }
  #index .van-swipe{
    width: 100%;
  }
  #index .van-swipe-item a>img{
    display: block;
    width: 100%;
    height: 150px;
  }
  #index .van-col--8{
    width: 20%!important;
    margin-top:10px;
  }
</style>



